@import url('https://fonts.googleapis.com/css2?family=Istok+Web&family=Karla&?family=Anonymous+Pro&display=swap');
@import url(//fonts.googleapis.com/earlyaccess/jejugothic.css);

#header {
    position: relative;
    background: #0A3950;
    padding-bottom:25px;
    padding-left:-10px;
    margin-top:-8px;
    width:100vw;
    z-index:5;
}

#title {
    /* sam bickerdike. */
    position:inline;
    display: flexbox;
    width:200px;
    padding-top:5px;
    transform: translateY(50%);
    font-weight: 400;
    font-size: 24px;
    font-family: 'Istok Web', sans-serif;
    margin-left:25px;
    margin-top:5px;
    margin-bottom:15px;
    /* identical to box height */
    color: #FFFFFF;
    border-right: 2px solid rgba(255,255,255,.75);
    font-size: 180%;
    text-align: center;
    white-space: nowrap;
    overflow: hidden;
}

/* Animation */
.anim-typewriter{
    animation: typewriter 1s steps(18) 1s 1 normal both,
               blinkTextCursor 500ms steps(44) infinite normal;
  }
  @keyframes typewriter{
    from{width: 0;}
    to{width: 200;}
  }
  @keyframes blinkTextCursor{
    from{border-right-color: rgba(255,255,255,.75);}
    to{border-right-color: transparent;}
}

#page {
    position:relative;
    margin-top:-32px;
    font-weight:100;
    float:right;
    border:2px solid transparent;
    margin-right:25px;
    font-family: 'Istok Web', sans-serif;
    font-size: 24px;
    color: #FFFFFF;

    transition: transform .5s ease;
    transform: scale(1.1); /* (150% zoom - Note: if the zoom is too large, it will go outside of the viewport) */
}

#page:hover{
    transition: transform .5s ease;
    transform: scale(1.1); /* (150% zoom - Note: if the zoom is too large, it will go outside of the viewport) */
}

#tableBody {
    position:relative;
    text-align:center;
    color:white;
    font-family: 'Istok Web', sans-serif;
    height:91.39vh;
    margin-left:-10px;
    margin-top:-2px;
}

.selfie {
    animation: 1s ease-out 0s 1 slideInFromLeft;
    background-image: url("../images/sam.jpg");
    background-size:cover;
    background-position-x: -200px;
    background-repeat: no-repeat;
    table-layout: fixed;
    width:50vw;
}

.contents {
    animation: 1s ease-out 0s 1 slideInFromRight;
    table-layout: fixed;
    font-family: 'Jeju Gothic', sans-serif;
    width:50vw;
    font-size: 26px;
}

.contents button {
    width: 296px;
    height: 55px;
    margin-top:50px;

    background: #00A6FB;
    box-shadow: 11px 11px 0px #003554;
    border: none;

    font-family: 'Istok Web', sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 24px;
    line-height: 52px;

    align-items: center;
    letter-spacing: 0.05em;

    color: #FFFFFF;
    transition: transform .5s ease;
    animation: fadeIn 2s;
}

body { background-color: black; }

.contents button:hover {
    transition: transform .5s ease;
    transform: scale(1.1); /* (150% zoom - Note: if the zoom is too large, it will go outside of the viewport) */
  }

#tsparticles {
    animation: fadeInTransition 3s;
    -webkit-animation: fadeInTransition 3s;
    -moz-animation: fadeInTransition 3s;
    -o-animation: fadeInTransition 3s;
    -ms-animation: fadeInTransition 3s;

    position: "absolute";
    height:"95%";
    width:"95%";
    z-index:-5;
}

.mobile-selfie {
    display:none;
    position: relative;
    width:75%;
    margin: 0 auto;
    padding-bottom:25px;
    border-radius: 100%;
}

@media only screen and (max-width: 650px) {
    .selfie {
        display: none;
    }

    .mobile-selfie {
        display:flex;
    }

    .contents {
        width:100%;
    }
}

@keyframes slideInFromLeft {
    0% {
        transform: translateX(-100%);
    }
      100% {
        transform: translateX(0);
    }
}

@keyframes slideInFromRight {
    0% {
        transform: translateX(100%);
    }
      100% {
        transform: translateX(0);
    }
}

@keyframes fadeIn {
    0% {opacity:0;}
    90% {opacity:0}
    100% {opacity:1;}
  }
  
  @-moz-keyframes fadeIn {
    0% {opacity:0;}
    90% {opacity:0.0}
    100% {opacity:1;}
  }
  
  @-webkit-keyframes fadeIn {
    0% {opacity:0;}
    90% {opacity:0}
    100% {opacity:1;}
  }
  
  @-o-keyframes fadeIn {
    0% {opacity:0;}
    90% {opacity:0}
    100% {opacity:1;}
  }
  
  @-ms-keyframes fadeIn {
    0% {opacity:0;}
    90% {opacity:0}
    100% {opacity:1;}
  }

  @keyframes fadeInTransition {
    0% {opacity:0;}
    100% {opacity:1;}
  }
  
  @-moz-keyframes fadeInTransition {
    0% {opacity:0;}
    100% {opacity:1;}
  }
  
  @-webkit-keyframes fadeInTransition {
    0% {opacity:0;}
    100% {opacity:1;}
  }
  
  @-o-keyframes fadeInTransition {
    0% {opacity:0;}
    100% {opacity:1;}
  }
  
  @-ms-keyframes fadeInTransition {
    0% {opacity:0;}
    100% {opacity:1;}
  }