.work {
    position:relative;
    text-align:center;
    color:white;
    font-family: 'Istok Web', sans-serif;
    width:99vw;
    margin-top:50px;
    margin-bottom:50px;
    animation: 1s fadeInTransition both normal;
    font-size: 180%;
    text-align: center;
    white-space: nowrap;
    overflow: hidden;
}

.workTable {
    margin: 0 auto;
    width: 80%;
    border-collapse:collapse;
}

.workTable td {
    width: 50%;
    text-align: left;
    animation: 1s 0.5s fadeInTransition both normal;
}

.workTable td img {
    width:350px;
    height:375px;
    transition: transform .5s ease;
    transform: scale(1.0); /* (150% zoom - Note: if the zoom is too large, it will go outside of the viewport) */
}

body {
    color:white;
    font-family: 'Istok Web', sans-serif;
}

.workTable td img:hover {
    transition: transform .5s ease;
    transform: scale(1.1); /* (150% zoom - Note: if the zoom is too large, it will go outside of the viewport) */
}

@keyframes fadeInTransition {
    0% {opacity:0;}
    100% {opacity:1;}
  }
  
  @-moz-keyframes fadeInTransition {
    0% {opacity:0;}
    100% {opacity:1;}
  }
  
  @-webkit-keyframes fadeInTransition {
    0% {opacity:0;}
    100% {opacity:1;}
  }
  
  @-o-keyframes fadeInTransition {
    0% {opacity:0;}
    100% {opacity:1;}
  }
  
  @-ms-keyframes fadeInTransition {
    0% {opacity:0;}
    100% {opacity:1;}
  }

  @media all and (max-width:1060px) {
    .workTable tr {    display: table;  width:100%;    }               
    .workTable td {    display: table-row; }
    .workTable{
        margin: 0 auto;
        width:33%;
    }
}